import {IndexModel} from "../../model";
const  indexModel = new IndexModel()
export default {
  name: "store",
  data() {
    return {
      title: "map",
      shops:[],
      latitude: 30.674958,
      longitude: 104.064283,
      address:1,
      markers: [
        // {
        //   iconPath: "https://imgcdn.tuogouchebao.com/location_logo@2x.png",
        //   id: 0,
        //   latitude: 30.689928,
        //   longitude: 104.049478,
        //   width: 24,
        //   height: 35,
        // },
        // {
        //   id: 1,
        //   latitude: 30.689928,
        //   longitude: 104.049478,
        //   iconPath: "https://imgcdn.tuogouchebao.com/location_logo@2x.png",
        //   width: 24,
        //   height: 35,
        // },
      ]
    };
  },
  components: {},
  methods: {
     toMy(e) {
     
      uni.switchTab({
        url: "/pages/my/my"
      });
    },
    toStore(){
       console.log('store')
      uni.switchTab({
        url: "/pages/store/store"
      });
    },
    toCarWash(){
      uni.switchTab({
        url:'/pages/car_wash/car_wash'
      })
    },
    toIndex() {
      uni.switchTab({
        url: "/pages/index/index"
      });
    },
     toMall() {
      uni.switchTab({
        url: "/pages/mall/mall"
      });
    },
    _choice_store() {
      uni.navigateTo({
        url: "/pagesB/choice_store/choice_store"
      });
    },
    _lately() {
      uni.navigateTo({
        url: "/pagesB/lately/lately"
      });
    },
    _navigation() {
      let self = this
      uni.openLocation({
        latitude: parseFloat(self.shops[0].latitude),
        longitude: parseFloat(self.shops[0].longitude),
        name: self.shops[0].name,
        address: self.shops[0].address,
        success: function() {
          console.log("success");
        }
      });
    }
  },
  mounted(){
      let self = this
      uni.getLocation({
          type: 'wgs84',
          success: function (res) {
              let params = {
                  longitude:res.longitude,
                  latitude:res.latitude,
              }
              indexModel.getShopList(params).then((res) => {
                  self.shops = res.data
                  for(let item of self.shops) {
                    self.markers.push({
                        iconPath :"https://imgcdn.tuogouchebao.com/location_logo@2x.png",
                        width:24,
                        height:35,
                        longitude:item.longitude,
                        latitude:item.latitude,
                    })
                  }
                  self.markers.push({
                    width:24,
                    height:35,
                    iconPath:'https://imgcdn.tuogouchebao.com/location_icon.png',
                    longitude:params.longitude,
                    latitude:params.latitude,
                  })
              })
          }
      }); 

    
  }
};
